import { Button, Container, Dialog } from '@mui/material';
import './App.css';
import QrCodeScannerIcon from '@mui/icons-material/QrCodeScanner';

import LectorQR from './components/LectorQR';
import { useState } from 'react';
function App() {

  const [open,setOpen] = useState(false);
  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };
  return (
    <div className="App">
      <Container>
        <h3>
          Lector de codigos QR del SAT
        </h3>
        <Button variant="outlined" startIcon={<QrCodeScannerIcon />} onClick={handleClickOpen}>
          Escanear código QR
        </Button>
        <Dialog
          fullScreen
          open={open}
          onClose={handleClose}
        >
          <LectorQR></LectorQR>
        </Dialog>
      </Container>
      
    </div>
  );
}

export default App;
